            // LOGIN
            #loginrow {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                width: 100%;
                margin-bottom: 1%;
            }
            
            #logincolumn {
                display: flex;
                flex-direction: column;
                flex-basis: 100%;
                flex: 1;
                width: 47%;
            }
            
            .form-column {
                height: 100px;
            }
            
            .heading-column {
                height: auto;
                margin-left: 40%;
                margin-top: 4%;
            }
            
            .form .login {
                justify-items: center;
                align-items: center;
            }
            
            #main-holder {
                height: 70%;
                display: grid;
                justify-items: center;
                align-items: center;
                background-color: white;
            }
            
            #login-error-msg-holder {
                width: 100%;
                height: 100%;
                display: grid;
                justify-items: center;
                align-items: center;
            }
            
            #login-error-msg {
                width: 26%;
                text-align: center;
                margin: 0;
                padding: 5px;
                font-size: 12px;
                font-weight: 500;
                color: #000;
                background-color: #82cedfb8;
                opacity: 0;
            }
            
            #error-msg-second-line {
                display: block;
            }
            
            #login-form {
                align-self: flex-start;
                display: grid;
                justify-items: center;
                align-items: center;
            }
            
            .login-form-field::placeholder {
                color: #838181fd;
                font-family: 'Dosis', sans-serif;
                font-size: 80%;
            }
            
            .placeholder:active,
            .login-form-field::placeholder:hover,
            .login-form-field::placeholder:visited {
                color: #0e8b91;
            }
            
            .login-form-field {
                border: none;
                border-bottom: 2px solid #99939394;
                margin-bottom: 10px;
                outline: none;
                padding: 0 46% 0 0;
            }
            
            .login-form-field:active,
            .login-form-field:hover,
            .login-form-field:visited {
                border-bottom: 2px solid #0e8b91;
            }
            
            #login-form-submit {
                @include button(100%);
